.countdown-card {
    margin: 5px 10px;
    padding: 10px;
    width: 800px;
    min-width: 180px;
    border-width: 1px;
    border-radius: 6px;
    border-color: white;
    border-style: solid;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-evenly;
    overflow: hidden;
    align-items: center;
}

@media screen and (max-width: 800px) {
    .countdown-card {
        width: 100%;
        margin: 5px 0;
    }
}

.countdown-card * {
    color: white;
}

.countdown-card.active {
    background-color: rgb(70,70,70);
}

.countdown-card .info {
    margin-left: 10px;
}

.countdown-card .title {
    font-size: 16pt;
    font-weight: bolder;
}

.countdown-card .date {
    margin-top: 10px;
    font-size: 10pt;
    font-weight: lighter;
}

.countdown-card .countdown {
    font-size: 11pt;
}

.countdown-card .countdown .subtitle {
    margin-top: 10px;
    font-weight: 600;
}
.countdown-card .icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60%;
    height: 80%;
    min-height: 16rem;
}
.countdown-card .info {
    width: 40%;
}
.countdown-card .icon img {
    width: 100%;
}

.card-container {
    display: flex;
    flex-flow: row wrap;
}
body {
    background-color: black;
    color: white;
}

body button {
    width: 100%;
    height: 100%;
}

.button-container {
    display: flex;
    flex-flow: column nowrap;
    padding: 7px;
    max-width: 64px;
}

body .content {
    display: flex;
    flex-flow: column nowrap;
}
.content img {
    width: 64px;
    height: 64px;
}
.content > * {
    padding-top: 10px;
    width: 100%;
    min-width: 400px;
    max-width: 80vw;
}

.defButtons {
    display: flex;
    flex-flow: row wrap;
    width: 100%;
}

.comp-container {
    width: 350px;
    margin-right: 5px;
    margin-bottom: 10px;
    padding: 10px;
    border-width: 1px;
    border-radius: 6px;
    border-color: white;
    border-style: solid;
}

.selection-container {
    padding-left: 15px;
}

.wins {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.loses {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.unit-button-selected {
    outline-style: dashed;
    outline-width: 4px;
}

body .banner {
    background-color: #2e5984;
    padding: 20px;
    text-align: center;
    color: white;
}

body .banner a {
    background-color: #2e5984;
    padding: 20px;
    text-align: center;
    color: white;
}